<script>
  import { onMount, onDestroy } from "svelte";
  import {
    Popover,
    PopoverContent,
    PopoverTrigger,
  } from "$lib/components/ui/popover";

  const {
    data = {
      transcript: "",
      cleaned: "",
      summary: "",
      reply: "",
    },
    show = false,
    loading = false,
    close = () => {},
    triggerElement = null, // Button element to attach popover to
  } = $props();

  const tabs = [
    { id: "transcript", label: "Transcript" },
    { id: "cleaned", label: "Cleaned" },
    { id: "summary", label: "Summary" },
    { id: "reply", label: "Suggested Reply" },
  ];

  let mounted = $state(false);
  let popoverOpen = $state(false);
  let copyTimeout = $state(null);
  let activeTab = $state("transcript");
  let copyButtonText = $state("Copy Text");
  let anchorElement;

  onMount(() => {
    mounted = true;
    if (show) {
      popoverOpen = true;
    }

    // Create and position the anchor element near the trigger button
    if (triggerElement) {
      positionAnchorElement();
    }
  });

  onDestroy(() => {
    if (copyTimeout) {
      clearTimeout(copyTimeout);
    }
  });

  $effect(() => {
    if (show) {
      popoverOpen = true;
      if (triggerElement) {
        positionAnchorElement();
      }
    } else {
      popoverOpen = false;
    }
  });

  function positionAnchorElement() {
    if (!triggerElement || !anchorElement) return;

    const rect = triggerElement.getBoundingClientRect();
    const scrollTop = window.scrollY || document.documentElement.scrollTop;
    const scrollLeft = window.scrollX || document.documentElement.scrollLeft;

    // Position the anchor at the same position as the trigger
    anchorElement.style.position = "absolute";
    anchorElement.style.top = `${rect.top + scrollTop}px`;
    anchorElement.style.left = `${rect.left + scrollLeft}px`;
    anchorElement.style.width = `${rect.width}px`;
    anchorElement.style.height = `${rect.height}px`;
    anchorElement.style.zIndex = "-1";
    anchorElement.style.opacity = "0";
    anchorElement.style.pointerEvents = "none";

    document.body.appendChild(anchorElement);
  }

  /** @param {string} tabId */
  function setActiveTab(tabId) {
    activeTab = tabId;
  }

  /** @param {string} text */
  function copyText(text) {
    navigator.clipboard.writeText(text);
    copyButtonText = "Copied!";
    if (copyTimeout) clearTimeout(copyTimeout);
    copyTimeout = setTimeout(() => (copyButtonText = "Copy Text"), 2000);
  }

  function handleOpenChange(open) {
    popoverOpen = open;
    if (!open) {
      close();
    }
  }
</script>

{#if mounted}
  <div bind:this={anchorElement}></div>

  <Popover open={popoverOpen} onOpenChange={handleOpenChange}>
    <PopoverTrigger asChild>
      {#if anchorElement}
        <div></div>
      {/if}
    </PopoverTrigger>

    <PopoverContent
      class="w-80 p-0 max-h-[450px] flex flex-col overflow-hidden"
      side="top"
      align="center"
    >
      <div
        class="flex justify-between items-center p-3 bg-gray-100 border-b border-gray-200"
      >
        <h2 class="font-semibold text-[#128c7e] text-base">Voice Message</h2>
        <button
          class="bg-transparent border-none text-gray-500 text-lg cursor-pointer w-6 h-6 flex items-center justify-center rounded-full transition-all hover:bg-gray-200 hover:text-gray-700"
          onclick={close}>✕</button
        >
      </div>

      <nav class="flex bg-gray-100 border-b border-gray-200">
        {#each tabs as tab}
          <button
            class={`
              flex-1 py-2 px-2 border-opacity-0 cursor-pointer transition-all flex flex-col
              items-center text-xs text-black
              ${
                activeTab === tab.id
                  ? "border-b-2 border-[#00a884] text-[#00a884] font-medium"
                  : "hover:bg-gray-200 bg-white border-gray-200"
              }
            `}
            disabled={loading || !data[tab.id]}
            onclick={() => setActiveTab(tab.id)}
            aria-selected={activeTab === tab.id}
            role="tab"
            aria-controls={`panel-${tab.id}`}
          >
            <span>{tab.label}</span>
          </button>
        {/each}
      </nav>

      <div class="p-4 overflow-y-auto flex-grow">
        {#if loading}
          <div class="flex flex-col items-center justify-center py-6">
            <div
              class="w-8 h-8 border-3 border-gray-200 border-t-[#00a884] rounded-full animate-spin mb-4"
            ></div>
            <p class="text-gray-500 text-sm">Processing voice message...</p>
          </div>
        {:else if data.transcript.startsWith("ERROR:")}
          <div class="flex flex-col items-center justify-center py-4">
            <div
              class="w-10 h-10 text-red-500 mb-2 flex items-center justify-center"
            >
              ⚠️
            </div>
            <p class="text-red-500 font-medium text-center text-sm mb-2">
              {data.transcript.replace("ERROR: ", "")}
            </p>
            <p class="text-gray-600 text-xs text-center">
              {data.cleaned}
            </p>
          </div>
        {:else}
          <div
            class="leading-relaxed"
            id={`panel-${activeTab}`}
            role="tabpanel"
            aria-labelledby={`tab-${activeTab}`}
          >
            <p class="m-0 whitespace-pre-wrap text-sm" dir="auto">
              {data[activeTab]}
            </p>
          </div>
        {/if}
      </div>

      <footer
        class="p-3 flex justify-end gap-2 border-t border-gray-200 bg-gray-100"
      >
        {#if !loading && data[activeTab]}
          <button
            disabled={!data[activeTab]}
            onclick={() => copyText(data[activeTab])}
            class="py-1.5 px-3 rounded-full text-xs cursor-pointer transition-all font-medium bg-[#00a884] text-white hover:bg-[#008f72] disabled:opacity-50 disabled:cursor-not-allowed"
          >
            {copyButtonText}
          </button>
        {/if}
      </footer>
    </PopoverContent>
  </Popover>
{/if}

<style>
  :global(.animate-spin) {
    animation: spin 1s linear infinite;
  }

  @keyframes spin {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(360deg);
    }
  }
</style>
